




























import {
  Component, Model, Prop, Vue,
} from 'vue-property-decorator';

@Component
export default class InputMultiSelector extends Vue {
  @Prop() txtLabel!: string;
  @Prop() txtPlaceholder!: string;
  @Prop({ default: () => [] }) main!: string[];
  @Prop({ default: () => [] }) sub!: string[];
  @Model('change', { default: '' }) readonly selected!: string[];
  isShow = false;

  mainHandler(opt: string) {
    if (opt !== this.selected[0]) this.$emit('change', [opt, '']);
  }

  subHandler(opt: string) {
    this.$emit('change', [this.selected[0], opt]);
  }

  get subList() {
    if (this.selected[0] && this.selected[0] !== '') {
      const idx = this.main.indexOf(this.selected[0]);
      return this.sub[idx][0];
    }
    return [];
  }
}
